
import { defineComponent } from "vue-demi";

export default defineComponent({
  emits: ["selectPage"],
  name: "TablePagination",
  props: {
    total: {
      type: Number,
      required: true,
    },
    query: {
      type: Object,
      required: true,
    },
  },
  methods: {
    onSelectPage(page: number) {
      this.$emit("selectPage", page);
    },
  },
});
